*{
    margin: 0;
    padding: 0;
}

.product-div{
    margin: 50px;
}


.right button{
    background-color: rgb(251, 143, 55);
    border: transparent;
    width: 60px;
    height: 30px;
    border-radius: 20px;
    font-weight: 600;
}

.info{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.product-name{
    display: flex;
    align-items: center;
    justify-content: center;
}

.product-price{
    display: flex;
    align-items: center;
    justify-content: center;
}

.product-name h2{
    font-size: 15px;
    margin-right: 15px;
    color: chocolate;
}

.product-price h2{
    font-size: 15px;
    margin-right: 15px;
    color: chocolate;
}

.image{
    object-fit:contain;
    border: 2px solid black;
    width: 300px;
    height: 150px;
}

/* -------- HOVER --------- */
.right button:hover{
    background-color: rgb(253, 162, 87);
    cursor: pointer;
}