*{
    margin: 0;
    padding: 0;
}

.basket-div{
    background-color: rgb(193, 193, 193);
    right: 0;
    position: absolute;
    margin-top: 29px;
    width: 200px;
    display: flex;
    border: 1px solid black;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 0 0 10px 10px;
}

.basket-products ul {
    
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.basket-products ul li{
    width: 200px;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fa{
   margin-top: 3px;
}

.buy-btn{
    margin-bottom: -2px;
    border: 1px solid black;
    height: 25px;
    color :#000;
    background-color: rgb(251, 143, 55);
    width: 100%;
    font-weight: 600;
    border-radius: 0 0 10px 10px;
}


/* -----  HOVER ----- */

.buy-btn:hover{
    cursor: pointer;
    background-color: rgb(253, 162, 87);
}

.fa:hover{
    cursor: pointer;
}