ul li{
  text-indent: 0;
  list-style-type: none;
}

.body{
  height: 100vh;
}

.header{
  border-bottom: 1px solid black;
  background-color: rgba(0, 0, 0, 0.989);
  height: 50px;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.logo{
  width: 100px;
}

.logo img{
  width: 100%;
  object-fit: cover;
}

.money{
  color: rgb(193, 193, 193);
  margin-right: 30px;
  font-size: 19px;
  font-weight: 500;
}

.header ul{
  display: flex;
}

.style{
  color: rgb(193, 193, 193);
  margin-right: 20px;
  font-size: 18px;
  font-weight: 500;
  border: transparent;
  background-color: transparent;
}

.icon{
  margin-right: 5px;
  width: 20px;
}

.products-div{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  
}

